import { Customer } from "app/entities/customer.entity";

export class CustomerUtils {
  static filteredCustomer(val: string, customers: Customer[]): Customer[] {
    var filteredCustomers =
      val && val != ""
        ? customers.filter(
            s =>
              s.businessName.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
              s.mobile.toLowerCase().indexOf(val.toLowerCase()) >= 0 ||
              s.gstNo.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : customers;
    return filteredCustomers;
  }
}



