import { StockItem } from "app/entities/stockitem.entity";

export class StockItemUtils {
  static filteredStockItems(val: string, stockItem: StockItem[]): StockItem[] {
    var filteredStockItem =
      val && val != ""
        ? stockItem.filter(
            s => s.productName.toLowerCase().indexOf(val.toLowerCase()) >= 0
          )
        : stockItem;
    return filteredStockItem;
  }
}



