import { Component, OnInit, ViewChild, SimpleChanges } from "@angular/core";
import { RestService } from "../../../common/rest.service";
import { AppService } from "../../../shared/service/app.service";
import { CRDN } from "./../../../entities/crdn.entity";
import { Storage } from "../../../shared/utils/storage";
import { NgForm } from "@angular/forms";
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators
} from "@angular/forms";
import {
  MdDialog,
  MdDialogRef,
  MD_DIALOG_DATA,
  MdPaginator
} from "@angular/material";
import { CRDNFormGroup } from "../../../forms/crdn.form";
import { PricingFormGroup } from "../../../forms/pricing.form";
import { Barcode } from "../../../entities/barcode.entity";
import { StockItem } from "../../../entities/stockitem.entity";
import { Service } from "./../../../entities/Service.entity";
import { PricingService } from "../../../common/services/pricing.service";
import { CustomerUtils } from "app/common/utils/customer.utils";
import { Customer } from "../../../entities/customer.entity";
import { CustomerPricing } from "../../../entities/customerpricing.entity";
import { StockItemPricing } from "../../../entities/stockitempricing.entity";
import { ServicePricing } from "../../../entities/servicepricing.entity";
import { StockItemUtils } from "../../../common/utils/stockitem.utils";
import { ServicesUtils } from "../../../common/utils/service.utils";
@Component({
  selector: "app-pricing",
  templateUrl: "./pricing.component.html",
  styleUrls: ["./pricing.component.scss"]
})
export class PricingComponent implements OnInit {
  //barcodeFrom: any = BARCODEFormGroup.init();
  // myForm: any = PricingFormGroup.init();
  index: number = 0;
  supplierList: any[] = [];
  filteredSuppliers: any[] = [];
  supplierSelected: string = "";
  isStockToggle: boolean = false;
  isServiceToggle: boolean = false;
  isCustomerToggle: boolean = true;
  customerPricing: CustomerPricing;
  stockItemPricing: StockItemPricing;
  servicePricing: ServicePricing;
  customerPricingList: CustomerPricing[] = [];
  stockItemPricingList: StockItemPricing[] = [];
  servicePricingList: ServicePricing[] = [];

  filteredStockItems: any[] = [];
  filteredServices: any[] = [];
  crdn: CRDN = new CRDN();
  isEditable: string = "true";
  isEdit: boolean = false;
  selectedItem: any;
  customers: Customer[] = [];

  filteredCustomers: any[] = [];
  stock: any[] = [];
  stockItemList: any[] = [];
  crdnFrom: any = CRDNFormGroup.init();
  constructor(
    private appService: AppService,
    public dialog: MdDialog,
    private pricingService: PricingService,
    private restService: RestService
  ) {
    // PricingFormGroup.pricingGroup(this.myForm);
  }

  ngOnInit() {
    this.pricingService.retreiveCustomers().subscribe(results => {
      this.filteredCustomers = results;
    });
    this.pricingService.retreiveStockItem().subscribe(results => {
      this.filteredStockItems = results;
    });
    this.pricingService.retreiveServices().subscribe(results => {
      this.filteredServices = results;
    });

    console.log("customer size is ", this.filteredCustomers.length);
    console.log("stockitem size is", this.filteredStockItems.length);
    console.log("services size is", this.filteredServices.length);

    this.addCustomer();
    this.addStockItem();
    this.addServices();
    this.isCustomerToggle = true;
    // this.customerPricing = new CustomerPricing();
    // form.reset();
  }

  addCustomer() {
    if (
      this.customerPricingList.length == 0 ||
      (this.customerPricingList[this.customerPricingList.length - 1]
        .customerId != null &&
        this.customerPricingList[this.customerPricingList.length - 1]
          .customerId != " ")
    ) {
      let item = new CustomerPricing();
      item.pricingDiscount = 0;
      item.customerId = null;
      this.customerPricingList.push(item);
      console.log(this.customerPricingList);
    }
  }

  addStockItem() {
    if (
      this.stockItemPricingList.length == 0 ||
      (this.stockItemPricingList[this.stockItemPricingList.length - 1]
        .stockItemId != null &&
        this.stockItemPricingList[this.stockItemPricingList.length - 1]
          .stockItemId != " ")
    ) {
      let item = new StockItemPricing();
      item.stockItemId = null;
      this.stockItemPricingList.push(item);
    }
  }
  addServices() {
    if (
      this.servicePricingList.length == 0 ||
      (this.servicePricingList[this.servicePricingList.length - 1].serviceId !=
        null &&
        this.servicePricingList[this.servicePricingList.length - 1].serviceId !=
          " ")
    ) {
      let item = new ServicePricing();
      item.pricingDiscount = 0;
      item.serviceId = null;
      this.servicePricingList.push(item);
    }
  }

  onCustomerToggleChange($event) {
    this.isCustomerToggle = true;
    this.isStockToggle = false;
    this.isServiceToggle = false;

    // if (this.customers.length == 0) {
    //   this.filteredCustomers = this.pricingService.retreiveCustomers();
    // }
    console.log("customer list" + this.customers.length);
  }

  filterCustomers(val: string) {
    this.filteredCustomers = CustomerUtils.filteredCustomer(
      val,
      this.filteredCustomers
    );
  }
  filterStockItems(val: string) {
    this.filteredStockItems = StockItemUtils.filteredStockItems(
      val,
      this.filteredStockItems
    );
  }
  filterServices(val: string) {
    this.filteredServices = ServicesUtils.filteredServices(
      val,
      this.filteredServices
    );
  }

  onStockToggleChange($event) {
    this.isStockToggle = true;
    this.isServiceToggle = false;
    this.isCustomerToggle = false;
  }

  onServiceToggleChange($event) {
    this.isServiceToggle = true;
    this.isStockToggle = false;
    this.isCustomerToggle = false;
  }
  selectLineItem(lineItem, index) {
    if (this.customerPricing != lineItem) {
      this.customerPricing = lineItem;
    }
  }

  selectLineItem1(lineItem, index) {
    if (this.stockItemPricing != lineItem) {
      this.stockItemPricing = lineItem;
    }
  }

  selectLineItem2(lineItem, index) {
    if (this.servicePricing != lineItem) {
      this.servicePricing = lineItem;
    }
  }
  itemValueChange(item: any) {
    let numberArray = Array(parseInt(item.pricingDiscount)).fill(1);
    console.log("numberArray", numberArray);
    // this.customerPricing.qtyArray = numberArray;
    this.addCustomer();
  }

  itemValueChange1(item: any) {
    let numberArray = Array(parseInt(item.pricingDiscount)).fill(1);
    console.log("numberArray", numberArray);
    // this.stockItemPricing.qtyArray = numberArray;
    this.addStockItem();
  }
  itemValueChange2(item: any) {
    let numberArray = Array(parseInt(item.pricingDiscount)).fill(1);
    console.log("numberArray", numberArray);
    // this.servicePricing.qtyArray = numberArray;
    this.addServices();
  }

  removeItem(index) {
    this.customerPricingList[index].customerId = null;
    if (this.customerPricingList.length > 0) {
      setTimeout(() => {
        this.customerPricingList.splice(index, 1);
      }, 10);
    }
  }

  removeItem1(index) {
    this.stockItemPricingList[index].stockItemId = null;
    if (this.stockItemPricingList.length > 0) {
      setTimeout(() => {
        this.stockItemPricingList.splice(index, 1);
      }, 10);
    }
  }
  removeItem2(index) {
    this.servicePricingList[index].serviceId = null;
    if (this.servicePricingList.length > 0) {
      setTimeout(() => {
        this.servicePricingList.splice(index, 1);
      }, 10);
    }
  }
  valid() {
    if (this.isStockToggle) this.crdn.serviceId = null;
    else this.crdn.productId = null;
    return true;
  }

  selectedCustomer(item: Customer) {
    this.customerPricing.customerId = item.displayName;
    this.customerPricing.customerId = item.id;
    console.log(this.customerPricing.customerId);
  }

  selectStockItem(item: StockItem) {
    this.stockItemPricing.stockItemId = item.productName;
    this.stockItemPricing.stockItemId = item.id;
  }

  selectServices(item: Service) {
    this.servicePricing.serviceId = item.serviceName;
    this.servicePricing.serviceId = item.id;
  }

  clearItem() {
    if (this.customerPricingList) {
      this.customerPricingList = [];
      setTimeout(() => {
        this.addCustomer();
      }, 10);
    }
    if (this.stockItemPricingList) {
      this.stockItemPricingList = [];
      setTimeout(() => {
        this.addStockItem();
      }, 10);
    }
    if (this.servicePricingList) {
      // servicePricingList
      this.servicePricingList = [];
      setTimeout(() => {
        this.addServices();
      }, 10);
    }
  }

  saveCustomer() {
    let customerPriceList = this.customerPricingList.filter(
      s => s.customerId != null
    );
    console.log(customerPriceList);
    this.pricingService
      .saveCustomerPricing(customerPriceList)
      .subscribe(result => {
        if (result) {
          this.customerPricing = result;
          console.log(this.customerPricing);

          this.appService.showMessage(
            "Customers Discount updated successfully"
          );
          this.clearItem();

          // this.customerPricing = new CustomerPricing();
          // form.reset();
          //  this.myForm.form.reset();
          // this.appService.navigate("pricing/add",[]);
        }
      });
  }
  saveStockItem() {
    let stockItemPriceList = this.stockItemPricingList.filter(
      s => s.stockItemId != null
    );
    console.log(stockItemPriceList);
    this.pricingService
      .saveStockItemPricing(stockItemPriceList)
      .subscribe(result => {
        if (result) {
          this.stockItemPricing = result;
          console.log(this.stockItemPricing);
          this.appService.showMessage("Products Discount Updated Successfully");
          this.clearItem();
        }
      });
  }
  saveServices() {
    let servicePriceList = this.servicePricingList.filter(
      s => s.serviceId != null
    );
    console.log(servicePriceList);
    this.pricingService
      .saveServicePricing(servicePriceList)
      .subscribe(result => {
        if (result) {
          this.servicePricing = result;
          console.log(this.servicePricing);
          this.appService.showMessage("Services Discount Updated Successfully");
          this.clearItem();
        }
      });
  }
  onlyNumberKey(event) {
    return event.charCode == 8 || event.charCode == 0
      ? null
      : event.charCode >= 48 && event.charCode <= 57;
  }
}



